import React from "react";
import Axios from "axios";

class Test extends React.Component {
  state = {
    post: []
  };

  componentDidMount() {
    return Axios.get("https://mapay.ga/api/Posts").then(res => {
      //console.log(JSON.stringify(res.data.data));

      //this.setState({ post: JSON.parse(JSON.stringify(res.data.data)) });
      this.setState({ post: res.data.data });
      //console.log(this.state.post);
      //this.state.post.map(item => console.log(item));
    });
  }

  render() {
    const { post } = this.state;
    return post.map(item => (
      <h4 key={item.id}>
        {item.profileimg} {item.name}
      </h4>
    ));
  }
}

export default Test;
