import React from "react";
import "../App.css";
import { Container, Spinner } from "react-bootstrap";
import ProductCard from "../com/ProductCard";
import Axios from "axios";
import { Redirect } from "react-router-dom";
import ProductComment from "../com/ProductComment";

class Product extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      product: null,
      loading: true,
      gotit: false
    };
  }
  componentDidMount() {
    Axios.get("https://mapay.ga/api/Products/" + this.props.match.params.id, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        this.setState({ product: res.data.data, gotit: true, loading: false });
        console.log("t", res.data.data);
      })
      .catch(erro => this.setState({ gotit: false, loading: false }));
  }
  render() {
    const { product } = this.state;
    return (
      <Container className="d-flex" style={{ minHeight: "100vh" }} fluid>
        {this.state.gotit ? (
          <Container className="my-5 m-auto text-left">
            <ProductCard
              name={product.name}
              price={product.price}
              text={product.discreption}
              img={product.image}
            />
            <ProductComment productid={this.state.product.id} />
          </Container>
        ) : this.state.loading ? (
          <Spinner className="m-auto" animation="grow" size="lg" />
        ) : (
          <Redirect to="/NotFound" />
        )}
      </Container>
    );
  }
}
export default Product;
