import React from "react";
import "../App.css";
import {
  Container,
  Row,
  Col,
  Card,
  Alert,
  Spinner,
  Button
} from "react-bootstrap";
import Axios from "axios";
import { MDBDataTable } from "mdbreact";

class Transaction extends React.Component {
  state = {
    userid: JSON.parse(localStorage.getItem("user")).id,
    Transaction: [],
    data: {
      columns: [
        {
          label: "Id",
          field: "id",
          sort: "asc",
          width: 150
        },
        {
          label: "Date",
          field: "Date",
          sort: "asc",
          width: 150
        },
        {
          label: "From",
          field: "fromname",
          sort: "asc",
          width: 270
        },

        {
          label: "Amount",
          field: "time",
          sort: "asc",
          width: 200
        },
        {
          label: "Verified",
          field: "office",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    },
    TError: false,
    TEText: <Spinner animation="grow" size="lg" />,
    funds: <Spinner animation="grow" />
  };

  componentDidMount() {
    Axios.get(
      "https://mapay.ga/api/Transactions/" +
        JSON.parse(localStorage.getItem("user")).id,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }
    ).then(res => {
      res.data.data.map(tran => {
        let data = { ...this.state.data };
        data.rows = [
          ...data.rows,
          {
            id: tran.id,
            time: tran.time,
            name:
              this.state.userid !== tran.fromuserid
                ? tran.fromname
                : tran.toname,
            funds:
              this.state.userid !== tran.fromuserid ? (
                <div>
                  <i className="fas fa-plus success-ic" /> {tran.funds}
                </div>
              ) : (
                <div>
                  <i className="fas fa-minus danger-ic" /> {tran.funds}
                </div>
              ),
            Verified:
              tran.validatedby !== null ? (
                <i className="fas fa-check success-ic" />
              ) : (
                <i className="fas fa-times danger-ic" />
              )
          }
        ];
        this.setState({ data: data });
        console.log(data);
        return 0;
      });
      this.setState({
        Transaction: res.data.data,
        TError: res.data.data.length > 0 ? true : false,
        TEText: res.data.data.length > 0 ? null : "There Is No Transactions"
      });
      console.log(res.data.data);
    });
    Axios.get(
      "https://mapay.ga/api/Wallets/" +
        JSON.parse(localStorage.getItem("user")).id,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }
    ).then(res => {
      console.log(res.data.data.funds);
      this.setState({ funds: res.data.data.funds });
    });
  }

  render() {
    const { funds } = this.state;
    return (
      <div className="App">
        <Container
          className="d-flex blue lighten-1 mb-5"
          style={{ minHeight: "20vh" }}
          fluid
        >
          <Container className="m-auto  text-center ">
            <h1>
              :) Hello Again, {JSON.parse(localStorage.getItem("user")).name}
            </h1>
          </Container>
        </Container>
        <Container className="my-5" style={{ minHeight: "70vh" }}>
          <Row>
            <Col md={3}>
              <Card>
                <Card.Header>Available Funds</Card.Header>
                <Card.Body>
                  <Card.Title>
                    <h1>${funds}</h1>
                  </Card.Title>
                </Card.Body>
              </Card>
            </Col>
            <Col md={9}>
              {this.state.TError ? (
                <MDBDataTable
                  responsive
                  striped
                  bordered
                  hover
                  data={this.state.data}
                />
              ) : (
                <Alert variant="light">{this.state.TEText}</Alert>
              )}
            </Col>
          </Row>
          <div id="datatable" className="d-none p-5">
            <MDBDataTable
              responsive
              striped
              bordered
              hover
              searching={false}
              paging={false}
              data={this.state.data}
            />
          </div>
          <Button
            onClick={() => {
              var mywindow = window.open("", "PRINT");
              mywindow.document.write(
                '<script src="https://kit.fontawesome.com/2f50f49aa5.js"></script><link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css" integrity="sha384-ggOyR0iXCbMQv3Xipma34MD+dH/1fQ784/j6cY/iJTQUOhcWr7x9JvoRxT2MZw1T" crossorigin="anonymous" />'
              );
              mywindow.document.write(
                document.getElementById("datatable").innerHTML
              );
              mywindow.document.write(
                '<script src="https://code.jquery.com/jquery-3.3.1.js"></script>'
              );
              mywindow.document.write(
                `<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/jquery-confirm/3.3.2/jquery-confirm.min.css"><script src="https://cdnjs.cloudflare.com/ajax/libs/jquery-confirm/3.3.2/jquery-confirm.min.js"></script><script>$.confirm({title: 'Confirm!',content: 'Do you wanna print?',buttons: {confirm: function () {$(".jconfirm-scrollpane").hide();window.print()},cancel: function () {window.close()}}});</script>`
              );
              mywindow.document.close();
              mywindow.focus();
            }}
          >
            PRINT
          </Button>
        </Container>
      </div>
    );
  }
}

export default Transaction;
