import React from "react";
import "../App.css";
import { Container, Row, Col, Card, Image, Spinner } from "react-bootstrap";
import Axios from "axios";
import { Link } from "react-router-dom";

class Explore extends React.Component {
  constructor() {
    super();
    this.state = {
      res: [],
      loading: true
    };
  }
  componentDidMount() {
    Axios.get("https://mapay.ga/api/Products", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    }).then(res => {
      this.setState({ res: res.data.data, loading: false });
      console.log(res.data.data);
      this.setState({
        TError: res.data.data.length > 0 ? true : false,
        TEText: res.data.data.length > 0 ? null : "There Is No Products"
      });
      console.log(res.data.data);
    });
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Container style={{ minHeight: "90vh" }} className="my-5">
            <Row>
              <Col className="">
                <div className="">
                  <Row className="justify-content-center">
                    {this.state.loading ? (
                      <Spinner animation="grow" size="lg" />
                    ) : (
                      this.state.res.map((item, index) => (
                        <Col key={"Res_" + index} md={4}>
                          <Card className="mx-n2 my-1">
                            <Card.Header
                              as={Link}
                              to={"/profile/" + item.user.id}
                              className="text-left"
                            >
                              <Image
                                style={{ height: "40px", width: "40px" }}
                                src={
                                  "https://picsum.photos/180/180?text=" +
                                  item.user.name
                                }
                                roundedCircle
                              />{" "}
                              {item.vendor.companyname}
                            </Card.Header>
                            <Link to={"/product/" + item.id}>
                              <Card.Img
                                src={item.image}
                                style={{ maxHeight: 500 }}
                                variant="top"
                              />
                              <Card.Body>
                                <Card.Title>{item.name}</Card.Title>
                                <Card.Text>{item.discreption}</Card.Text>
                              </Card.Body>
                            </Link>
                          </Card>
                        </Col>
                      ))
                    )}
                  </Row>
                </div>
              </Col>
            </Row>
          </Container>
        </header>
      </div>
    );
  }
}

export default Explore;
