import React from "react";
import "../App.css";
import {
  Container,
  Form,
  Button,
  Col,
  Tab,
  Tabs,
  Spinner,
  Alert
} from "react-bootstrap";
import Axios from "axios";

class Add extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      key: "Step1",
      id: JSON.parse(localStorage.getItem("user")).id,
      funds: "",
      loading: false,
      alertText: "",
      variant: "",
      allert: false
    };
  }

  fundsHandleChane = e => {
    this.setState({ funds: e.target.value });
  };

  handlesubmit = e => {
    e.preventDefault();
    this.setState({ loading: true, alertText: "", variant: "", allert: false });
    let login = {
      id: this.state.id,
      funds: this.state.funds
    };

    Axios.put("https://mapay.ga/api/Wallets", login, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        console.log(res);
        this.setState({
          key: "Step1",
          funds: "",
          loading: false,
          alertText: "Funds Successfully Added",
          variant: "success",
          allert: true
        });
        login = {};
      })
      .catch(error => {
        // handle error
        console.log(JSON.stringify(error));
        console.log(login);
        this.setState({
          loading: false,
          alertText: "Error: " + error.message,
          variant: "danger",
          allert: true
        });
        login = {};
      });
  };

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Container
            className="d-flex blue lighten-1 mb-5"
            style={{ minHeight: "20vh" }}
            fluid
          >
            <Container className="m-auto  text-center ">
              <h1>
                :) Hello Again, {JSON.parse(localStorage.getItem("user")).name}
              </h1>
            </Container>
          </Container>

          <Container className="d-flex mb-5" style={{ minHeight: "80vh" }}>
            <Container className="m-auto w-100 text-center ">
              <Form onSubmit={this.handlesubmit} className="m-auto w-100 ">
                <h1>Add Money</h1>
                <hr color="white" style={{ height: "0.3px", width: "60%" }} />
                {this.state.loading ? (
                  <Spinner animation="grow" size="lg" />
                ) : this.state.allert ? (
                  <Alert variant={this.state.variant}>
                    {this.state.alertText}
                  </Alert>
                ) : null}
                <Tabs
                  activeKey={this.state.key}
                  onSelect={key => this.setState({ key })}
                >
                  <Tab eventKey="Step1" title="Step 1">
                    <Form.Row>
                      <Col>
                        <Form.Group controlId="money">
                          <Form.Label>Amount</Form.Label>
                          <Form.Control
                            type="number"
                            placeholder="Money Amount"
                            onChange={this.fundsHandleChane}
                            value={this.state.funds}
                          />
                        </Form.Group>
                      </Col>
                    </Form.Row>

                    <Button
                      className="m-1"
                      variant="warning"
                      onClick={e => {
                        this.setState({
                          key: "Step2"
                        });
                      }}
                    >
                      Next
                    </Button>
                  </Tab>

                  <Tab eventKey="Step2" title="Step 2">
                    <Form.Row>
                      <Col>
                        <Form.Group controlId="nameoncard">
                          <Form.Label>Name on card</Form.Label>
                          <Form.Control
                            type="Name"
                            placeholder="Name on Card"
                          />
                        </Form.Group>
                      </Col>
                      <Col>
                        <Form.Group controlId="postcode">
                          <Form.Label>Post Code</Form.Label>
                          <Form.Control
                            type="postcode"
                            placeholder="Post Code"
                          />
                        </Form.Group>
                      </Col>
                    </Form.Row>
                    <Form.Row>
                      <Col>
                        <Form.Group controlId="cardnumber">
                          <Form.Label>Card number</Form.Label>
                          <Form.Control
                            type="number"
                            placeholder="Card number"
                          />
                        </Form.Group>
                      </Col>
                    </Form.Row>
                    <Form.Row>
                      <Col>
                        <Form.Group controlId="expirationMonth">
                          <Form.Label>Expiration Month</Form.Label>
                          <Form.Control type="Expiration" placeholder="MM" />
                        </Form.Group>
                      </Col>
                      <Col>
                        <Form.Group controlId="expirationYear">
                          <Form.Label>Expiration Year</Form.Label>
                          <Form.Control type="Expiration" placeholder="YY" />
                        </Form.Group>
                      </Col>
                      <Col>
                        <Form.Group controlId="cvv">
                          <Form.Label>CVV</Form.Label>
                          <Form.Control type="CVV" placeholder="123" />
                        </Form.Group>
                      </Col>
                    </Form.Row>
                    <Button
                      className="m-1"
                      variant="warning"
                      onClick={e => {
                        this.setState({
                          key: "Step1"
                        });
                      }}
                    >
                      Previous
                    </Button>
                    <Button variant="primary" type="submit">
                      Add Money
                    </Button>
                  </Tab>
                </Tabs>
              </Form>
            </Container>
          </Container>
        </header>
      </div>
    );
  }
}
export default Add;
