import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import SideNav, { NavItem, NavIcon, NavText } from "@trendmicro/react-sidenav";
import "@trendmicro/react-sidenav/dist/react-sidenav.css";
import AddProduct from "../AddProduct";
import VProducts from "../Productlist";
import Transaction from "../Transaction";
import RedimCoupon from "../RedimCoupon";
import AddPost from "../Post";
import Postslist from "../Postlist";

class Vendor extends Component {
  render() {
    return (
      <div className="ml-5" style={{ minHeight: "100vh" }}>
        <Router>
          <Route
            render={({ location, history }) => (
              <React.Fragment>
                <SideNav
                  onSelect={selected => {
                    const to = "/Vendor/" + selected;
                    if (location.pathname !== to) {
                      history.push(to);
                    }
                  }}
                >
                  <SideNav.Toggle />
                  <SideNav.Nav defaultSelected="Products">
                    <NavItem eventKey="addproduct">
                      <NavIcon>
                        <i
                          className="fa fa-fw fa-plus"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Add Product</NavText>
                    </NavItem>
                    <NavItem eventKey="post">
                      <NavIcon>
                        <i
                          className="fa fa-fw fa-share"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Add Post</NavText>
                    </NavItem>
                    <NavItem eventKey="Posts">
                      <NavIcon>
                        <i
                          className="fa fa-fw fa-money-check"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Posts</NavText>
                    </NavItem>
                    <NavItem eventKey="Products">
                      <NavIcon>
                        <i
                          className="fa fa-clipboard-list"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Products</NavText>
                    </NavItem>
                    <NavItem eventKey="Sells">
                      <NavIcon>
                        <i
                          className="fas fa-chart-line"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Sells</NavText>
                    </NavItem>
                    <NavItem eventKey="Redim">
                      <NavIcon>
                        <i
                          className="fas fa-cash-register"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Redim Coupon</NavText>
                    </NavItem>
                  </SideNav.Nav>
                </SideNav>
                <main className="w-auto ml-1">
                  <Route path="/Vendor/" exact component={VProducts} />
                  <Route
                    path="/Vendor/addproduct"
                    component={props => <AddProduct />}
                  />
                  <Route path="/Vendor/post" component={props => <AddPost />} />
                  <Route
                    path="/Vendor/Products"
                    component={props => <VProducts />}
                  />
                  <Route
                    path="/Vendor/Sells"
                    component={props => <Transaction />}
                  />
                  <Route
                    path="/Vendor/Redim"
                    component={props => <RedimCoupon />}
                  />
                  <Route
                    path="/Vendor/Posts"
                    component={props => <Postslist />}
                  />
                </main>
              </React.Fragment>
            )}
          />
        </Router>
      </div>
    );
  }
}

export default Vendor;
