import React from "react";
import { Form, Button, Col, Alert } from "react-bootstrap";
import TextareaAutosize from "react-textarea-autosize";
import Axios from "axios";

class ContactForm extends React.Component {
  state = {
    name: "",
    email: "",
    phone: "",
    subject: "",
    message: "",
    allert: false
  };
  nameHandleChane = e => {
    this.setState({ name: e.target.value });
  };

  emailHandleChane = e => {
    this.setState({ email: e.target.value });
  };
  phoneHandleChane = e => {
    this.setState({ phone: e.target.value });
  };

  subjectHandleChane = e => {
    this.setState({ subject: e.target.value });
  };
  messageHandleChane = e => {
    this.setState({ message: e.target.value });
  };

  handlesubmit = e => {
    e.preventDefault();
    const contact = {
      name: this.state.name,
      eamil: this.state.email,
      phone: this.state.phone,
      subject: this.state.subject,
      message: this.state.message,
      checked: false
    };

    Axios.post("https://mapay.ga/api/Contacts", contact, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then(res => {
        console.log(res);
        this.setState({
          name: "",
          email: "",
          phone: "",
          subject: "",
          message: "",
          allert: true
        });
      })
      .catch(error => {
        // handle error
        console.log(error);
      });
  };
  render() {
    return (
      <Form onSubmit={this.handlesubmit} className="m-auto w-100">
        <h1>Contact</h1>
        <hr color="white" style={{ height: "0.3px", width: "60%" }} />
        {this.state.allert ? (
          <Alert variant={"success"}>Message Successfully Sent</Alert>
        ) : null}
        <Form.Row>
          <Form.Group as={Col} controlId="Name">
            <Form.Label>Full Name</Form.Label>
            <Form.Control
              onChange={this.nameHandleChane}
              value={this.state.name}
              type="name"
              placeholder="Enter Name"
              required
            />
          </Form.Group>

          <Form.Group as={Col} controlId="Email">
            <Form.Label>Email address</Form.Label>
            <Form.Control
              onChange={this.emailHandleChane}
              value={this.state.email}
              type="email"
              placeholder="Enter Email"
              required
            />
          </Form.Group>
        </Form.Row>

        <Form.Row>
          <Form.Group as={Col} controlId="Phone">
            <Form.Label>Phone</Form.Label>
            <Form.Control
              onChange={this.phoneHandleChane}
              value={this.state.phone}
              type="phone"
              placeholder="Enter Phone"
              required
            />
          </Form.Group>

          <Form.Group as={Col} controlId="Subject">
            <Form.Label>Subject</Form.Label>
            <Form.Control
              onChange={this.subjectHandleChane}
              value={this.state.subject}
              type="subject"
              placeholder="Enter Subject"
              required
            />
          </Form.Group>
        </Form.Row>

        <Form.Group controlId="Message">
          <Form.Label>Message</Form.Label>
          <TextareaAutosize
            onChange={this.messageHandleChane}
            value={this.state.message}
            className="form-control"
            style={{ resize: "none" }}
            minRows={5}
            placeholder="Enter Message"
            required
          />
        </Form.Group>

        <Button variant="primary" type="submit">
          Submit Now
        </Button>
      </Form>
    );
  }
}

export default ContactForm;
