import React from "react";
import { Card, CardColumns, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import Axios from "axios";

class ProfileProducts extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      vendorid: props.userid,
      products: [],
      gotit: false
    };
  }
  componentDidMount() {
    Axios.get("https://mapay.ga/api/VProducts/" + this.state.vendorid, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        let cards = [];
        const length = res.data.data.length > 3 ? 3 : res.data.data.length;
        for (let i = 0; i < length; i++) {
          let element = res.data.data[i];
          cards = [...cards, element];
          console.log(element);
        }
        this.setState({ products: cards, gotit: true });

        console.log("t", res.data.data);
        console.log(length);
      })
      .catch();
  }

  render() {
    const { products } = this.state;
    return this.state.gotit ? (
      <div>
        <CardColumns className="my-5">
          {products.map(res => (
            <Card key={res.id} as={Link} to={"/product/" + res.id}>
              <Card.Body>
                <Card.Title>{res.name}</Card.Title>
              </Card.Body>
              <Card.Img
                src={res.image}
                style={{ maxHeight: 500 }}
                variant="bottom"
              />
            </Card>
          ))}
        </CardColumns>

        <div className="d-flex justify-content-center align-items-center">
          <Link
            className="btn my-5 btn-primary"
            as={Button}
            to={"/products/" + this.state.vendorid}
          >
            See More Products <i className="fas fa-arrow-right" />
          </Link>
        </div>
      </div>
    ) : null;
  }
}

export default ProfileProducts;
