import React from "react";

function ProductCard(props) {
  return (
    <div className="jumbotron text-center hoverable p-4">
      <div className="row">
        <div className="col-md-4 offset-md-1 mx-3 my-3">
          <div className="view overlay">
            <img src={props.img} className="img-fluid" alt="" />
            <a href="#!">
              <div className="mask rgba-white-slight" />
            </a>
          </div>
        </div>
        <div className="col-md-7 text-md-left ml-3 mt-3">
          <h4 className="h4 mb-4">{props.name}</h4>
          <a href="#!" className="green-text">
            <h6 className="h6 pb-1">
              <i className="fas fa-dollar-sign pr-1" /> {props.price}
            </h6>
          </a>
          <p className="font-weight-normal">{props.text}</p>
        </div>
      </div>
    </div>
  );
}

export default ProductCard;
