import React from "react";
import "../App.css";
import { Container, Form, Button, Col, Spinner, Alert } from "react-bootstrap";
import Axios from "axios";
import Select from "react-select";

class RedimCoupon extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      key: "Step1",
      id: JSON.parse(localStorage.getItem("user")).id,
      funds: 0,
      coupon: "",
      loading: false,
      alertText: "",
      variant: "",
      allert: false,
      selectedOption: null,
      options: []
    };
  }

  componentDidMount() {
    console.log("mount");

    Axios.get("https://mapay.ga/api/VProducts/" + this.state.id, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        let options = [];
        let prices = [];
        const data = res.data.data;
        for (let i = 0; i < data.length; i++) {
          let element = {
            value: data[i].id,
            label: data[i].name + data[i].price
          };
          prices[data[i].id] = data[i].price;
          options = [...options, element];
          console.log(element);
        }
        this.setState({ options: options, prices: prices });

        console.log("t", res.data.data);
        console.log(data.length);
      })
      .catch(err => {
        console.log(err);
      });
  }

  fundsHandleChane = e => {
    this.setState({ funds: e.target.value });
  };

  handleChange = selectedOption => {
    this.setState({ selectedOption });
    console.log(`Option selected:`, selectedOption);
    if (selectedOption !== null) {
      let funds = 0;
      selectedOption.forEach(option => {
        funds += this.state.prices[option.value];
      });

      this.setState({ funds });
      console.log(this.state.funds);
    } else {
      this.setState({ funds: 0 });
      console.log(this.state.funds);
    }
  };

  couponHandleChane = e => {
    this.setState({ coupon: e.target.value });
  };

  handlesubmit = e => {
    e.preventDefault();
    this.setState({ loading: true, alertText: "", variant: "", allert: false });
    let coupon = {
      userid: this.state.id,
      funds: this.state.funds,
      coupon: this.state.coupon
    };

    Axios.put("https://mapay.ga/api/Coupons", coupon, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        console.log(res);
        this.setState({
          funds: 0,
          coupon: "",
          loading: false,
          alertText: "Funds Successfully Added",
          variant: "success",
          allert: true
        });
      })
      .catch(error => {
        // handle error
        console.log(JSON.stringify(error));
        console.log(coupon);
        this.setState({
          loading: false,
          alertText: "Error: " + error.message,
          variant: "danger",
          allert: true
        });
        coupon = {};
      });
  };

  render() {
    const { selectedOption } = this.state;
    return (
      <div className="App">
        <header className="App-header">
          <Container className="d-flex mb-5" style={{ minHeight: "80vh" }}>
            <Container className="m-auto w-100 text-center ">
              <Form onSubmit={this.handlesubmit} className="m-auto w-100 ">
                <h1>Redim Code</h1>
                <hr color="white" style={{ height: "0.3px", width: "60%" }} />
                {this.state.loading ? (
                  <Spinner animation="grow" size="lg" />
                ) : this.state.allert ? (
                  <Alert variant={this.state.variant}>
                    {this.state.alertText}
                  </Alert>
                ) : null}
                <Form.Row>
                  <Col>
                    <Form.Group controlId="money">
                      <Form.Label>Amount</Form.Label>
                      <Select
                        isMulti
                        value={selectedOption}
                        onChange={this.handleChange}
                        options={this.state.options}
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Form.Row>
                  <Col>
                    <Form.Group controlId="money">
                      <Form.Label>Coupon</Form.Label>
                      <Form.Control
                        type="number"
                        placeholder="Coupon Code"
                        onChange={this.couponHandleChane}
                        value={this.state.coupon}
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Form.Row>
                  <Col>
                    <Form.Group controlId="money">
                      <Form.Label>Total</Form.Label>
                      <Form.Control
                        type="number"
                        placeholder="Money Amount"
                        readOnly
                        value={this.state.funds}
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Button variant="primary" type="submit">
                  Add Money
                </Button>
              </Form>
            </Container>
          </Container>
        </header>
      </div>
    );
  }
}
export default RedimCoupon;
