import React from "react";
import "../App.css";
import { Container } from "react-bootstrap";
import MyJumbtron1 from "../com/jumptron1";
import CardImage from "../com/cardimage";
import jumbotron1 from "../jsonfiles/jumbtron1";
import cardimage1 from "../jsonfiles/cardimage1";

class About extends React.Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Container
            style={{
              background: "no-repeat center center scroll",
              backgroundSize: "cover",
              backgroundImage:
                'url("http://www.bccg-events.de/bild/webseite/kopfneu_2_182447.jpg")',
              minHeight: "30vh"
            }}
            fluid
          />
          <Container className="my-5">
            <h1>About Us</h1>

            <CardImage
              card={cardimage1}
              button={{ href: "/register", text: "Join Us Now" }}
            />
            <MyJumbtron1 jumbotron={jumbotron1} />
          </Container>
        </header>
      </div>
    );
  }
}
export default About;
