import React from "react";
import {
  Card,
  Form,
  Col,
  Button,
  ListGroup,
  ListGroupItem,
  Image,
  Spinner
} from "react-bootstrap";
import Textarea from "react-textarea-autosize";
import Axios from "axios";

class ProductComment extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      comments: [],
      loading: true,
      comment: ""
    };
  }
  componentDidMount() {
    Axios.get("https://mapay.ga/api/Rateproducts/" + this.props.productid, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    }).then(res => {
      this.setState({ comments: res.data.data, loading: false });
      console.log(this.state.comments);
    });
  }

  commentHandleChane = e => {
    this.setState({ comment: e.target.value });
    console.log(this.state.comment);
  };

  comment = e => {
    e.preventDefault();
    console.log(this.state.comment);
    const comment = {
      rating: 6,
      comment: this.state.comment,
      productid: this.props.productid,
      customerid: JSON.parse(localStorage.getItem("customer")).id
    };
    if (comment != null) {
      Axios.post("https://mapay.ga/api/Rateproducts", comment, {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        console.log(res);
        this.setState({ comment: "" });
      });
      const list = document.getElementById("comment_");

      const ListGroup = document.createElement("div");
      ListGroup.classList.add("list-group-item");
      const h5 = document.createElement("h5");
      const Image = document.createElement("img");
      Image.setAttribute(
        "src",
        "https://picsum.photos/180/180?text=" +
          JSON.parse(localStorage.getItem("user")).name
      );
      Image.setAttribute("style", "height: 40px; width: 40px;");
      Image.classList.add("rounded-circle");
      h5.appendChild(Image);
      h5.appendChild(
        document.createTextNode(
          " " + JSON.parse(localStorage.getItem("user")).name
        )
      );
      ListGroup.appendChild(h5);
      ListGroup.appendChild(document.createTextNode(comment.comment));
      list.appendChild(ListGroup);
    }
  };

  render() {
    return this.state.loading ? (
      <div className="d-flex">
        <Spinner className="m-auto" animation="grow" size="lg" />
      </div>
    ) : localStorage.getItem("customer") || this.state.comments.length > 0 ? (
      <Card>
        <Card.Body>
          <ListGroup id="comment_" className="list-group-flush">
            {this.state.comments.map(res => (
              <ListGroupItem key={res.id}>
                <h5>
                  <Image
                    style={{ height: "40px", width: "40px" }}
                    src={"https://picsum.photos/180/180?text=" + res.user.name}
                    roundedCircle
                  />
                  {" " + res.user.name}
                </h5>
                {res.comment}
              </ListGroupItem>
            ))}
          </ListGroup>
          {localStorage.getItem("customer") ? (
            <Form onSubmit={this.comment} className="my-1">
              <Form.Row className="align-items-center">
                <Col>
                  <Textarea
                    value={this.state.comment}
                    onChange={this.commentHandleChane}
                    className="form-control"
                    style={{ resize: "none" }}
                    placeholder="Comment"
                  />
                </Col>
                <Col md={"auto"}>
                  <Button type="submit" className="btn-sm btn-rounded">
                    Comment
                  </Button>
                </Col>
              </Form.Row>
            </Form>
          ) : null}
        </Card.Body>
      </Card>
    ) : null;
  }
}

export default ProductComment;
