import React from "react";
import "../App.css";
import { Container, Form, Button, Alert } from "react-bootstrap";
import Axios from "axios";

class AddProduct extends React.Component {
  state = {
    name: "",
    discreption: "",
    price: "",
    vendorid: JSON.parse(localStorage.getItem("vendor")).id,
    image: "",
    alertText: "",
    variant: "",
    allert: false
  };

  nameHandleChane = e => {
    this.setState({ name: e.target.value });
    console.log(this.state.name);
  };

  discreptionHandleChane = e => {
    this.setState({ discreption: e.target.value });
    console.log(this.state.discreption);
  };

  priceHandleChane = e => {
    this.setState({ price: e.target.value });
    console.log(this.state.price);
  };

  imageHandleChane = e => {
    this.setState({ image: e.target.value });
    console.log(this.state.image);
  };

  handlesubmit = e => {
    e.preventDefault();
    const product = {
      name: this.state.name,
      discreption: this.state.discreption,
      price: this.state.price,
      image: this.state.image,
      vendorid: this.state.vendorid
    };

    Axios.post("https://mapay.ga/api/Products", product, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        console.log(res.data);
        console.log(product);
        this.setState({
          name: "",
          discreption: "",
          price: "",
          image: "",
          vendorid: JSON.parse(localStorage.getItem("vendor")).id,
          alertText: "Account Successfully Created",
          variant: "success",
          allert: true
        });
      })
      .catch(error => {
        // handle error
        console.log(error);
        console.log(product);
        this.setState({
          alertText: "Error: " + error.message,
          variant: "danger",
          allert: true
        });
      });
  };

  render() {
    return (
      <div className="App">
        <Container className="d-flex" style={{ minHeight: "100vh" }}>
          <Form onSubmit={this.handlesubmit} className="m-auto w-100">
            <h1>Add Product</h1>
            <hr color="white" style={{ width: "60%" }} />
            {this.state.allert ? (
              <Alert variant={this.state.variant}>{this.state.alertText}</Alert>
            ) : null}
            <Form.Group controlId="name">
              <Form.Label>Product Name</Form.Label>
              <Form.Control
                onChange={this.nameHandleChane}
                value={this.state.name}
                type="name"
                placeholder="Product Name"
              />
            </Form.Group>

            <Form.Group controlId="discreption">
              <Form.Label>discreption</Form.Label>
              <Form.Control
                onChange={this.discreptionHandleChane}
                value={this.state.discreption}
                type="discreption"
                placeholder="discreption"
              />
            </Form.Group>

            <Form.Group controlId="price">
              <Form.Label>price</Form.Label>
              <Form.Control
                onChange={this.priceHandleChane}
                value={this.state.price}
                type="price"
                placeholder="price"
              />
            </Form.Group>

            <Form.Group controlId="price">
              <Form.Label>image</Form.Label>
              <Form.Control
                onChange={this.imageHandleChane}
                value={this.state.image}
                type="text"
                placeholder="image Link"
              />
            </Form.Group>

            {/* <Form.Group controlId="Image">
              <Form.Label>Image</Form.Label>
              <div class="input-group">
                <div class="custom-file">
                  <Form.Control
                    className="custom-file-input"
                   
                    type="file"
                    placeholder="Image"
                  />
                  <label
                    class="custom-file-label text-left text-black-50"
                    for="inputGroupFile01"
                  >
                    Choose file
                  </label>
                </div>
              </div>
            </Form.Group> */}
            <Button className="m-1" variant="primary" type="submit">
              Add Product
            </Button>
          </Form>
        </Container>
      </div>
    );
  }
}
export default AddProduct;
