import React from "react";
import { Col, Card, Row } from "react-bootstrap";

function LitleCard(props) {
  return (
    <Row className="text-left my-5">
      {props.card.map((card, index) => (
        <Col key={index} md={4} sm={12}>
          <Card className="my-2">
            <Card.Img
              style={{
                height: 200,
                backgroundImage: "url(" + card.img + ")",
                backgroundSize: "cover",
                backgroundPosition: "center"
              }}
              variant="top"
            />
            <Card.Body>
              <Card.Title>{card.Title}</Card.Title>
              <Card.Subtitle className="mb-2 text-muted">
                {card.Subtitle}
              </Card.Subtitle>
              <Card.Text style={{ fontSize: "0.6em" }}>{card.Text}</Card.Text>
            </Card.Body>
          </Card>
        </Col>
      ))}
    </Row>
  );
}

export default LitleCard;
