import React from "react";
import "../App.css";
import { Container, Form, Button, Col, Spinner, Alert } from "react-bootstrap";
import Axios from "axios";

class Buy extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      id: JSON.parse(localStorage.getItem("user")).id,
      funds: "",
      loading: false,
      alertText: "",
      variant: "",
      allert: false
    };
  }

  fundsHandleChane = e => {
    this.setState({ funds: e.target.value });
  };

  handlesubmit = e => {
    e.preventDefault();
    this.setState({ loading: true, alertText: "", variant: "", allert: false });
    let coupon = {
      userid: this.state.id,
      funds: this.state.funds
    };

    Axios.post("https://mapay.ga/api/Coupons", coupon, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        console.log(res);
        this.setState({
          funds: "",
          loading: false,
          alertText:
            "Coupon Successfully Added, Coupon: " + res.data.data.coupon,
          variant: "success",
          allert: true
        });
        coupon = {};
      })
      .catch(error => {
        // handle error
        console.log(JSON.stringify(error));
        console.log(coupon);
        this.setState({
          loading: false,
          alertText: "Error: " + error.message,
          variant: "danger",
          allert: true
        });
        coupon = {};
      });
  };

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Container
            className="d-flex blue lighten-1 mb-5"
            style={{ minHeight: "20vh" }}
            fluid
          >
            <Container className="m-auto  text-center ">
              <h1>
                :) Hello Again, {JSON.parse(localStorage.getItem("user")).name}
              </h1>
            </Container>
          </Container>

          <Container className="d-flex mb-5" style={{ minHeight: "80vh" }}>
            <Container className="m-auto w-100 text-center ">
              <Form onSubmit={this.handlesubmit} className="m-auto w-100 ">
                <h1>Buy Coupon</h1>
                <hr color="white" style={{ height: "0.3px", width: "60%" }} />
                {this.state.loading ? (
                  <Spinner animation="grow" size="lg" />
                ) : this.state.allert ? (
                  <Alert variant={this.state.variant}>
                    {this.state.alertText}
                  </Alert>
                ) : null}
                <Form.Row>
                  <Col>
                    <Form.Group controlId="money">
                      <Form.Label>Amount</Form.Label>
                      <Form.Control
                        type="number"
                        placeholder="Money Amount"
                        onChange={this.fundsHandleChane}
                        value={this.state.funds}
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Button variant="primary" type="submit">
                  Buy Coupon
                </Button>
              </Form>
            </Container>
          </Container>
        </header>
      </div>
    );
  }
}
export default Buy;
