import React from "react";
import "../App.css";
import { Container, Form, Button, Alert } from "react-bootstrap";
import Axios from "axios";
class Send extends React.Component {
  state = {
    fromuserid: JSON.parse(localStorage.getItem("user")).id,
    touserid: "",
    funds: "",
    time: "",
    validatedby: null,
    validateddate: null,
    alertText: "",
    variant: "",
    allert: false
  };
  touseridHandleChane = e => {
    this.setState({ touserid: e.target.value });
  };

  fundsHandleChane = e => {
    this.setState({ funds: e.target.value });
  };

  handlesubmit = e => {
    e.preventDefault();
    const transaction = {
      fromuserid: this.state.fromuserid,
      touserid: this.state.touserid,
      funds: this.state.funds,
      time: this.state.time,
      validatedby: this.state.validatedby,
      validateddate: this.state.validateddate
    };

    Axios.post("https://mapay.ga/api/Transactions", transaction, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        console.log(res);
        this.setState({
          fromuserid: JSON.parse(localStorage.getItem("user")).id,
          touserid: "",
          funds: "",
          time: "",
          validatedby: null,
          validateddate: null,
          alertText: "Funds Successfully Sent",
          variant: "success",
          allert: true
        });
      })
      .catch(error => {
        // handle error
        console.log(JSON.stringify(error));
        this.setState({
          alertText: "Error: " + error.message,
          variant: "danger",
          allert: true
        });
      });
  };

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Container
            className="d-flex blue lighten-1 mb-5"
            style={{ minHeight: "20vh" }}
            fluid
          >
            <Container className="m-auto  text-center ">
              <h1>
                :) Hello Again, {JSON.parse(localStorage.getItem("user")).name}
              </h1>
            </Container>
          </Container>

          <Container className="d-flex mb-5" style={{ minHeight: "80vh" }}>
            <Container className="m-auto w-100 text-center ">
              <h1>Send Money</h1>
              <hr color="white" style={{ height: "0.3px", width: "60%" }} />
              {this.state.allert ? (
                <Alert variant={this.state.variant}>
                  {this.state.alertText}
                </Alert>
              ) : null}
              <Form onSubmit={this.handlesubmit} className="m-auto w-100 ">
                <Form.Group controlId="id">
                  <Form.Label>Send money to</Form.Label>
                  <Form.Control
                    onChange={this.touseridHandleChane}
                    value={this.state.touserid}
                    type="number"
                    placeholder="Enter Account ID"
                  />
                </Form.Group>

                <Form.Group controlId="money">
                  <Form.Label>Amount</Form.Label>
                  <Form.Control
                    onChange={this.fundsHandleChane}
                    value={this.state.funds}
                    type="number"
                    placeholder="money amount"
                  />
                </Form.Group>

                <Button className="m-1" variant="primary" type="submit">
                  Send
                </Button>
              </Form>
            </Container>
          </Container>
        </header>
      </div>
    );
  }
}
export default Send;
