import React from "react";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";

function CardImage(props) {
  return props.card ? (
    <div
      className="card card-image mt-5"
      style={{
        backgroundImage: "url(" + props.card.img + ")",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center center"
      }}
    >
      <div className="text-white text-center rgba-stylish-strong py-5 px-4">
        <div className="py-5">
          <h5 className="h5 orange-text">
            <i className="fas fa-desktop" /> {props.card.hint}
          </h5>
          <h2 className="card-title h2 my-4 py-2">{props.card.title}</h2>
          <p className="mb-4 pb-2 px-md-5 mx-md-5">{props.card.text}</p>
          {props.button ? (
            <Link
              as={Button}
              to={props.button.href}
              className="btn btn-success"
            >
              <i className="fas fa-clone left" /> {props.button.text}
            </Link>
          ) : null}
        </div>
      </div>
    </div>
  ) : null;
}

export default CardImage;
