import React from "react";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";

function MyJumbtron1(props) {
  return props.jumbotron ? (
    <div className="jumbotron text-center hoverable p-4">
      <div className="row">
        <div className="col-md-4 offset-md-1 mx-3 my-3">
          <div className="view overlay">
            <img src={props.jumbotron.img} className="img-fluid" alt="" />
            <a href="#!">
              <div className="mask rgba-white-slight" />
            </a>
          </div>
        </div>
        <div className="col-md-7 text-md-left ml-3 mt-3">
          <a href="#!" className="green-text">
            <h6 className="h6 pb-1">
              <i className="fas fa-desktop pr-1" /> {props.jumbotron.hint}
            </h6>
          </a>
          <h4 className="h4 mb-4">{props.jumbotron.title}</h4>
          {props.jumbotron.text}
          {props.button ? (
            <Link
              as={Button}
              to={props.button.href}
              className="btn btn-success"
            >
              {props.button.text}
            </Link>
          ) : null}
        </div>
      </div>
    </div>
  ) : null;
}

export default MyJumbtron1;
