import React from "react";
import { Card, CardColumns } from "react-bootstrap";
import { Link } from "react-router-dom";
import Axios from "axios";

class ProductList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      vendorid: props.userid,
      products: [],
      gotit: false
    };
  }
  componentDidMount() {
    Axios.get("https://mapay.ga/api/VProducts/" + this.state.vendorid, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        let cards = [];
        const length = res.data.data.length;
        for (let i = 0; i < length; i++) {
          let element = res.data.data[i];
          cards = [...cards, element];
          console.log(element);
        }
        this.setState({ products: cards, gotit: true });

        console.log("t", res.data.data);
        console.log(length);
      })
      .catch();
  }
  render() {
    const { products } = this.state;
    return this.state.gotit ? (
      <CardColumns className="my-5">
        {products.map(res => (
          <Card as={Link} to={"/product/" + res.id}>
            <Card.Img
              src={res.image}
              style={{ maxHeight: 500 }}
              variant="top"
            />
            <Card.Body>
              <Card.Title>{res.name}</Card.Title>
              <Card.Text>{res.discreption}</Card.Text>
            </Card.Body>
          </Card>
        ))}
      </CardColumns>
    ) : null;
  }
}

export default ProductList;
