import React from "react";
import "../App.css";
import { Container, Row, Col, Alert, Spinner, Card } from "react-bootstrap";
import Axios from "axios";
import { MDBDataTable } from "mdbreact";

class Coupons extends React.Component {
  state = {
    userid: JSON.parse(localStorage.getItem("user")).id,
    data: {
      columns: [
        {
          label: "Id",
          field: "id",
          sort: "desc",
          width: 150
        },
        {
          label: "Funds",
          field: "funds",
          sort: "asc",
          width: 270
        },
        {
          label: "Coupon",
          field: "coupon",
          sort: "asc",
          width: 270
        },
        {
          label: "Exp Date",
          field: "time",
          sort: "asc",
          width: 150
        },
        {
          label: "Active",
          field: "active",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    },
    TError: false,
    TEText: <Spinner animation="grow" size="lg" />,
    funds: <Spinner animation="grow" />
  };

  componentDidMount() {
    Axios.get(
      "https://mapay.ga/api/Coupons/" +
        JSON.parse(localStorage.getItem("user")).id,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }
    ).then(res => {
      res.data.data.map(coupon => {
        let data = { ...this.state.data };
        data.rows = [
          ...data.rows,
          {
            id: coupon.id,
            funds: coupon.funds,
            coupon: coupon.coupon,
            time: coupon.exp,
            active:
              coupon.active === 1 ? (
                <i className="fas fa-check success-ic" />
              ) : (
                <i className="fas fa-times danger-ic" />
              )
          }
        ];
        this.setState({ data: data });
        console.log(data);
        console.log(res.data.data);
        return 0;
      });
      this.setState({
        TError: res.data.data.length > 0 ? true : false,
        TEText: res.data.data.length > 0 ? null : "There Is No Coupons"
      });
      console.log(res.data.data);
    });
    Axios.get(
      "https://mapay.ga/api/Wallets/" +
        JSON.parse(localStorage.getItem("user")).id,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }
    ).then(res => {
      console.log(res.data.data.funds);
      this.setState({ funds: res.data.data.funds });
    });
  }

  render() {
    const { funds } = this.state;

    return (
      <div className="App">
        <Container
          className="d-flex blue lighten-1 mb-5"
          style={{ minHeight: "20vh" }}
          fluid
        >
          <Container className="m-auto  text-center ">
            <h1>
              :) Hello Again, {JSON.parse(localStorage.getItem("user")).name}
            </h1>
          </Container>
        </Container>
        <Container className="my-5" style={{ minHeight: "100vh" }}>
          <Row>
            <Col md={3}>
              <Card>
                <Card.Header>Available Funds</Card.Header>
                <Card.Body>
                  <Card.Title>
                    <h1>${funds}</h1>
                  </Card.Title>
                </Card.Body>
              </Card>
            </Col>
            <Col md={9}>
              {this.state.TError ? (
                <MDBDataTable
                  responsive
                  striped
                  bordered
                  hover
                  data={this.state.data}
                />
              ) : (
                <Alert variant="light">{this.state.TEText}</Alert>
              )}
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Coupons;
