import React from "react";
import "../App.css";
import { Container } from "react-bootstrap";
import ProfileCover from "../com/ProfileCover";
import ProductList from "../com/ProductList";

class Products extends React.Component {
  render() {
    return (
      <Container style={{ minHeight: "100vh" }} fluid>
        <ProfileCover />
        <Container className="my-5 text-left">
          <ProductList userid={this.props.match.params.id} />
        </Container>
      </Container>
    );
  }
}
export default Products;
