import React from "react";
import { Container } from "react-bootstrap";
import "../App.css";
import MyCarousel from "../com/mycarousel";
import LitleCard from "../com/litlecard";
import MyJumbtron1 from "../com/jumptron1";
import card1 from "../jsonfiles/card1";
import card2 from "../jsonfiles/card2";
import ContactForm from "../com/contact";
import { jumbotron1, jumbotron2 } from "../jsonfiles/img";
import list from "../jsonfiles/list";
import { Redirect } from "react-router-dom";

class Home1 extends React.Component {
  constructor() {
    super();
    this.state = {
      list: list
    };
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
          {localStorage.getItem("admin") || localStorage.getItem("vendor") ? (
            <Redirect
              to={process.env.PUBLIC_URL + "/" + localStorage.getItem("rule")}
            />
          ) : localStorage.getItem("customer") ? (
            <Redirect to={process.env.PUBLIC_URL + "/home"} />
          ) : null}
          <MyCarousel list={this.state.list} />
          <Container className="">
            <LitleCard card={card1} />
            <MyJumbtron1
              jumbotron={jumbotron1}
              button={{ href: "/register", text: "Join Us Now" }}
            />
            <LitleCard card={card2} />
            <MyJumbtron1
              jumbotron={jumbotron2}
              button={{ href: "/about", text: "Read More" }}
            />

            <div className="my-5">
              <ContactForm />
            </div>
          </Container>
        </header>
      </div>
    );
  }
}

export default Home1;
