import React from "react";
import "../App.css";
import { Container, Alert, Spinner, Button } from "react-bootstrap";
import Axios from "axios";
import { MDBDataTable } from "mdbreact";

class Postslist extends React.Component {
  state = {
    userid: JSON.parse(localStorage.getItem("user")).id,
    Transaction: [],
    data: {
      columns: [
        {
          label: "Id",
          field: "id",
          sort: "asc",
          width: 150
        },
        {
          label: "text",
          field: "text",
          sort: "asc",
          width: 150
        },
        {
          label: "Date",
          field: "created_at",
          sort: "asc",
          width: 270
        },

        {
          label: "Comments count",
          field: "coments",
          sort: "asc",
          width: 200
        },
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    },
    TError: false,
    TEText: <Spinner animation="grow" size="lg" />,
    funds: <Spinner animation="grow" />
  };

  componentDidMount() {
    this.setState({
      data: {
        columns: this.state.data.columns,
        rows: []
      }
    });
    Axios.get(
      "https://mapay.ga/api/Posts/" +
        JSON.parse(localStorage.getItem("user")).id,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }
    ).then(res => {
      res.data.data.map(prod => {
        let data = { ...this.state.data };
        data.rows = [
          ...data.rows,
          {
            id: prod.id,
            text: prod.text,
            created_at:
              prod.created_at.substring(0, 10) +
              " " +
              prod.created_at.substring(11, 19),
            coment: Object.keys(prod.comment.data).length,
            action: (
              <Button
                id={prod.id}
                variant="danger"
                onClick={this.deleteproduct.bind(this)}
                size="sm"
              >
                Delete
              </Button>
            )
          }
        ];
        this.setState({ data: data });
        console.log(data);
        return 0;
      });
      this.setState({
        Transaction: res.data.data,
        TError: res.data.data.length > 0 ? true : false,
        TEText: res.data.data.length > 0 ? null : "There Is No Products"
      });
      console.log(res.data.data);
    });
  }

  deleteproduct(e) {
    this.setState({
      TError: false,
      TEText: <Spinner animation="grow" size="lg" />,
      funds: <Spinner animation="grow" />
    });
    Axios.delete("https://mapay.ga/api/Posts/" + e.target.id, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    }).then(res => {
      console.log(res.data);
      this.componentDidMount();
    });
  }

  render() {
    return (
      <div className="App">
        <Container className="my-5" style={{ minHeight: "70vh" }}>
          {this.state.TError ? (
            <MDBDataTable
              responsive
              striped
              bordered
              hover
              data={this.state.data}
            />
          ) : (
            <Alert variant="light">{this.state.TEText}</Alert>
          )}
        </Container>
      </div>
    );
  }
}

export default Postslist;
