import React from "react";
import "../App.css";
import {
  Container,
  Form,
  Button,
  Col,
  Tabs,
  Tab,
  Alert
} from "react-bootstrap";
import { Link } from "react-router-dom";
import Axios from "axios";
import DatePicker from "react-datepicker";

class Register extends React.Component {
  constructor() {
    super();
    this.state = {
      key: "Step1",
      birthdate: new Date()
    };
  }

  state = {
    firstname: "",
    lastname: "",
    email: "",
    password: "",
    confirmPassword: "",
    address: "",
    address2: "",
    city: "",
    state: "",
    postcode: 0,
    cin: "",
    birthdate: new Date(),
    cellphone: "",
    homephone: "",
    alertText: "",
    variant: "",
    allert: false
  };
  firstnameHandleChane = e => {
    this.setState({ firstname: e.target.value });
    console.log(this.state.firstname);
  };

  lastnameHandleChane = e => {
    this.setState({ lastname: e.target.value });
    console.log(this.state.lastname);
  };

  emailHandleChane = e => {
    this.setState({ email: e.target.value });
    console.log(this.state.email);
  };

  passwordHandleChane = e => {
    this.setState({ password: e.target.value });
    console.log(this.state.password);
  };

  confirmPasswordHandleChane = e => {
    this.setState({ confirmPassword: e.target.value });
    console.log(this.state.confirmPassword);
  };

  addressHandleChane = e => {
    this.setState({ address: e.target.value });
    console.log(this.state.address);
  };

  address2HandleChane = e => {
    this.setState({ address2: e.target.value });
    console.log(this.state.address2);
  };

  cityHandleChane = e => {
    this.setState({ city: e.target.value });
    console.log(this.state.city);
  };

  stateHandleChane = e => {
    this.setState({ state: e.target.value });
    console.log(this.state.state);
  };

  postcodeHandleChane = e => {
    this.setState({ postcode: e.target.value });
    console.log(this.state.postcode);
  };

  cinHandleChane = e => {
    this.setState({ cin: e.target.value });
    console.log(this.state.cin);
  };

  birthdateHandleChane = e => {
    this.setState({ birthdate: e });
    console.log(this.state.birthdate);
  };

  cellphoneHandleChane = e => {
    this.setState({ cellphone: e.target.value });
    console.log(this.state.cellphone);
  };

  homephoneHandleChane = e => {
    this.setState({ homephone: e.target.value });
    console.log(this.state.homephone);
  };

  handlesubmit = e => {
    e.preventDefault();
    const register = {
      name: this.state.firstname + " " + this.state.lastname,
      email: this.state.email,
      password: this.state.password,
      confirmPassword: this.state.confirmPassword,
      address: this.state.address,
      address2: this.state.address2,
      city: this.state.city,
      state: this.state.state,
      postcode: this.state.postcode,
      cin: this.state.cin,
      birthdate: this.state.birthdate,
      cellphone: this.state.cellphone,
      homephone: this.state.homephone
    };
    if (register.confirmPassword === register.password) {
      Axios.post("https://mapay.ga/api/register", register, {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json"
        }
      })
        .then(res => {
          console.log(res.data);
          console.log(register);
          this.setState({
            firstname: "",
            lastname: "",
            email: "",
            password: "",
            confirmPassword: "",
            address: "",
            address2: "",
            city: "",
            state: "",
            postcode: null,
            cin: "",
            birthdate: new Date(),
            cellphone: "",
            homephone: "",
            alertText: "Account Successfully Created",
            variant: "success",
            allert: true
          });
        })
        .catch(error => {
          // handle error
          console.log(error);
          console.log(register);
          this.setState({
            alertText: "Error: " + error.message,
            variant: "danger",
            allert: true
          });
        });
    } else {
      this.setState({
        alertText: "Error: Confirm Password",
        variant: "danger",
        allert: true
      });
    }
  };

  render() {
    return (
      <div className="App">
        <Container className="d-flex" style={{ minHeight: "100vh" }}>
          <Form onSubmit={this.handlesubmit} className="m-auto w-100">
            <h1>Register</h1>
            <hr color="white" style={{ height: "0.3px", width: "60%" }} />
            {this.state.allert ? (
              <Alert variant={this.state.variant}>{this.state.alertText}</Alert>
            ) : null}
            <Tabs
              activeKey={this.state.key}
              onSelect={key => this.setState({ key })}
            >
              <Tab eventKey="Step1" title="Step 1">
                <Form.Row>
                  <Col>
                    <Form.Group controlId="FirstName">
                      <Form.Label>FirstName </Form.Label>
                      <Form.Control
                        onChange={this.firstnameHandleChane}
                        value={this.state.firstname || ""}
                        type="FirstName"
                        placeholder="First Name"
                      />
                    </Form.Group>
                  </Col>
                  <Col>
                    <Form.Group controlId="LastName">
                      <Form.Label>LastName</Form.Label>
                      <Form.Control
                        onChange={this.lastnameHandleChane}
                        value={this.state.lastname || ""}
                        type="LastName"
                        placeholder="Last Name"
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Form.Row>
                  <Col>
                    <Form.Group controlId="email">
                      <Form.Label>Email </Form.Label>
                      <Form.Control
                        onChange={this.emailHandleChane}
                        value={this.state.email || ""}
                        type="email"
                        placeholder="Email"
                      />
                    </Form.Group>
                  </Col>
                  <Col>
                    <Form.Group controlId="birthdate">
                      <Form.Label>Year</Form.Label>
                      <Form.Control
                        as={DatePicker}
                        selected={this.state.birthdate}
                        onChange={this.birthdateHandleChane}
                        value={this.state.birthdate || ""}
                        peekNextMonth
                        showMonthDropdown
                        showYearDropdown
                        dropdownMode="select"
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Form.Row>
                  <Col>
                    <Form.Group controlId="password">
                      <Form.Label>Password</Form.Label>
                      <Form.Control
                        onChange={this.passwordHandleChane}
                        value={this.state.password || ""}
                        type="password"
                        placeholder="Password"
                      />
                    </Form.Group>
                  </Col>
                  <Col>
                    <Form.Group controlId="ConfirmPassword">
                      <Form.Label>Confirm Password</Form.Label>
                      <Form.Control
                        onChange={this.confirmPasswordHandleChane}
                        value={this.state.confirmPassword || ""}
                        type="password"
                        placeholder="Confirm Password"
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Button
                  className="m-1"
                  variant="warning"
                  onClick={e => {
                    this.setState({
                      key: "Step2"
                    });
                  }}
                >
                  Next
                </Button>
                <Link className="m-1" to="/login">
                  Already Have Account?
                </Link>
                <Link className="m-1" to="/registerv">
                  Register for Business Account?
                </Link>
              </Tab>
              <Tab eventKey="Step2" title="Step 2">
                <Form.Row>
                  <Col>
                    <Form.Group controlId="Address">
                      <Form.Label>Address</Form.Label>
                      <Form.Control
                        onChange={this.addressHandleChane}
                        value={this.state.address || ""}
                        type="address"
                        placeholder="Address"
                      />
                    </Form.Group>
                  </Col>
                  <Col>
                    <Form.Group controlId="Address2">
                      <Form.Label>Address 2</Form.Label>
                      <Form.Control
                        onChange={this.address2HandleChane}
                        value={this.state.address2 || ""}
                        type="address2"
                        placeholder="Address 2"
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Form.Row>
                  <Col>
                    <Form.Group controlId="City">
                      <Form.Label>City </Form.Label>
                      <Form.Control
                        onChange={this.cityHandleChane}
                        value={this.state.city || ""}
                        type="city"
                        placeholder="City"
                      />
                    </Form.Group>
                  </Col>
                  <Col>
                    <Form.Group controlId="State">
                      <Form.Label>State</Form.Label>
                      <Form.Control
                        onChange={this.stateHandleChane}
                        value={this.state.state || ""}
                        type="State"
                        placeholder="State"
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Form.Row>
                  <Col>
                    <Form.Group controlId="PostCode">
                      <Form.Label>PostCode</Form.Label>
                      <Form.Control
                        onChange={this.postcodeHandleChane}
                        value={this.state.postcode || ""}
                        type="PostCode"
                        placeholder="Post Code"
                      />
                    </Form.Group>
                  </Col>
                  <Col>
                    <Form.Group controlId="CIN">
                      <Form.Label>CIN</Form.Label>
                      <Form.Control
                        onChange={this.cinHandleChane}
                        value={this.state.cin || ""}
                        type="CIN"
                        placeholder="CIN"
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Button
                  className="m-1"
                  variant="warning"
                  onClick={e => {
                    this.setState({
                      key: "Step1"
                    });
                  }}
                >
                  Previous
                </Button>
                <Button
                  className="m-1"
                  variant="warning"
                  onClick={e => {
                    this.setState({
                      key: "Step3"
                    });
                  }}
                >
                  Next
                </Button>
              </Tab>
              <Tab eventKey="Step3" title="Step 3">
                <Form.Row>
                  <Col>
                    <Form.Group controlId="CellPhone">
                      <Form.Label>CellPhone</Form.Label>
                      <Form.Control
                        onChange={this.cellphoneHandleChane}
                        value={this.state.cellphone || ""}
                        type="CellPhone"
                        placeholder="CellPhone"
                      />
                    </Form.Group>
                  </Col>
                  <Col>
                    <Form.Group controlId="HomePhone">
                      <Form.Label>HomePhone </Form.Label>
                      <Form.Control
                        onChange={this.homephoneHandleChane}
                        value={this.state.homephone || ""}
                        type="HomePhone"
                        placeholder="Enter Home Phone"
                      />
                    </Form.Group>
                  </Col>
                </Form.Row>
                <Button
                  className="m-1"
                  variant="warning"
                  onClick={e => {
                    this.setState({
                      key: "Step2"
                    });
                  }}
                >
                  previous
                </Button>
                <Button className="m-1" variant="primary" type="submit">
                  Register
                </Button>
              </Tab>
            </Tabs>
          </Form>
        </Container>
      </div>
    );
  }
}
export default Register;
