import React from "react";
import "../App.css";
import { Container, Form, Button, Spinner, Alert } from "react-bootstrap";
import { Link } from "react-router-dom";
import Axios from "axios";

class Login extends React.Component {
  state = {
    email: "",
    password: "",
    loading: false,
    alertText: "",
    variant: "",
    allert: false
  };
  emailHandleChane = e => {
    this.setState({ email: e.target.value });
  };

  passwordHandleChane = e => {
    this.setState({ password: e.target.value });
  };

  handlesubmit = e => {
    e.preventDefault();
    this.setState({ loading: true, alertText: "", variant: "", allert: false });
    const login = {
      email: this.state.email,
      password: this.state.password
    };

    Axios.post("https://mapay.ga/api/login", login, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then(res => {
        console.log(res);
        localStorage.setItem("token", JSON.stringify(res.data.token));
        localStorage.setItem("user", JSON.stringify(res.data.user));
        localStorage.setItem(
          res.data.customer
            ? "customer"
            : res.data.vendor
            ? "vendor"
            : res.data.admin
            ? "admin"
            : res.data.superadmin
            ? "superadmin"
            : null,
          JSON.stringify(
            res.data.customer
              ? res.data.customer
              : res.data.vendor
              ? res.data.vendor
              : res.data.admin
              ? res.data.admin
              : res.data.superadmin
              ? res.data.superadmin
              : null
          )
        );
        localStorage.setItem(
          "rule",
          res.data.customer
            ? "customer"
            : res.data.vendor
            ? "vendor"
            : res.data.admin
            ? "admin"
            : res.data.superadmin
            ? "superadmin"
            : null
        );
        this.setState({ loading: false });
      })
      .catch(error => {
        // handle error
        console.log(login);
        this.setState({
          loading: false,
          alertText: "Error: " + error.message,
          variant: "danger",
          allert: true
        });
      });
  };

  render() {
    return (
      <div className="App">
        {localStorage.getItem("rule") ? (window.location.href = "/") : null}
        <Container className="d-flex" style={{ minHeight: "100vh" }}>
          <Form className="m-auto w-100" onSubmit={this.handlesubmit}>
            <h1>Login</h1>
            <hr color="white" style={{ width: "60%" }} />
            {this.state.loading ? (
              <Spinner className="m-auto" animation="grow" size="lg" />
            ) : this.state.allert ? (
              <Alert variant={this.state.variant}>{this.state.alertText}</Alert>
            ) : null}
            <Form.Group controlId="formBasicEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter Email"
                onChange={this.emailHandleChane}
              />
              <Form.Text className="text-muted">
                We'll never share your email with anyone else.
              </Form.Text>
            </Form.Group>

            <Form.Group controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Password"
                onChange={this.passwordHandleChane}
              />
            </Form.Group>
            <Form.Group controlId="formBasicChecbox">
              <Form.Check name="remember" type="checkbox" label="Remember me" />
            </Form.Group>
            <Button className="m-1" variant="primary" type="submit">
              Login
            </Button>
            <Link to="/register">Create Account?</Link>
          </Form>
        </Container>
      </div>
    );
  }
}
export default Login;
