import React from "react";
import "../App.css";
import { Container, Form, Button, Alert } from "react-bootstrap";
import Axios from "axios";

class AddPost extends React.Component {
  state = {
    text: "",
    userid: JSON.parse(localStorage.getItem("user")).id,
    image: "",
    alertText: "",
    variant: "",
    allert: false
  };

  textHandleChane = e => {
    this.setState({ text: e.target.value });
    console.log(this.state.text);
  };

  imageHandleChane = e => {
    this.setState({ image: e.target.value });
    console.log(this.state.image);
  };

  handlesubmit = e => {
    e.preventDefault();
    const Post = {
      text: this.state.text,
      image: this.state.image,
      userid: this.state.userid
    };

    Axios.post("https://mapay.ga/api/Posts", Post, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        console.log(res.data);
        console.log(Post);
        this.setState({
          text: "",
          image: "",
          userid: JSON.parse(localStorage.getItem("user")).id,
          alertText: "Post Successfully Created",
          variant: "success",
          allert: true
        });
      })
      .catch(error => {
        // handle error
        console.log(error);
        console.log(Post);
        this.setState({
          alertText: "Error: " + error.message,
          variant: "danger",
          allert: true
        });
      });
  };

  render() {
    return (
      <div className="App">
        <Container className="d-flex" style={{ minHeight: "100vh" }}>
          <Form onSubmit={this.handlesubmit} className="m-auto w-100">
            <h1>Add Post</h1>
            <hr color="white" style={{ width: "60%" }} />
            {this.state.allert ? (
              <Alert variant={this.state.variant}>{this.state.alertText}</Alert>
            ) : null}

            <Form.Group controlId="text">
              <Form.Label>text</Form.Label>
              <Form.Control
                onChange={this.textHandleChane}
                value={this.state.text}
                type="text"
                placeholder="text"
              />
            </Form.Group>

            <Form.Group controlId="image">
              <Form.Label>image</Form.Label>
              <Form.Control
                onChange={this.imageHandleChane}
                value={this.state.image}
                type="text"
                placeholder="image Link"
              />
            </Form.Group>

            {/* <Form.Group controlId="Image">
              <Form.Label>Image</Form.Label>
              <div class="input-group">
                <div class="custom-file">
                  <Form.Control
                    className="custom-file-input"
                   
                    type="file"
                    placeholder="Image"
                  />
                  <label
                    class="custom-file-label text-left text-black-50"
                    for="inputGroupFile01"
                  >
                    Choose file
                  </label>
                </div>
              </div>
            </Form.Group> */}
            <Button className="m-1" variant="primary" type="submit">
              Publish
            </Button>
          </Form>
        </Container>
      </div>
    );
  }
}
export default AddPost;
