import React from "react";
import "../App.css";
import { Container } from "react-bootstrap";
import MyJumbtron1 from "../com/jumptron1";
import jumbotron2 from "../jsonfiles/jumbtron2";
import cardimage2 from "../jsonfiles/cardimage2";
import CardImage from "../com/cardimage";
class Services extends React.Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Container
            style={{
              background: "no-repeat center center scroll",
              backgroundSize: "cover",
              backgroundImage:
                'url("https://techdissected.com/wp-content/uploads/2018/09/Startups-Image-1-2500x500.jpeg")',
              minHeight: "30vh"
            }}
            fluid
          />

          <Container className="my-5">
            <h1>Services</h1>

            <CardImage
              card={cardimage2}
              button={{ href: "/register", text: "Join Us Now" }}
            />

            <MyJumbtron1 jumbotron={jumbotron2} />
          </Container>
        </header>
      </div>
    );
  }
}
export default Services;
