import React from "react";
import { Navbar, Nav, NavDropdown, Image } from "react-bootstrap";
import { Link } from "react-router-dom";
import logo from "../logo.svg";

class MyNavbar extends React.Component {
  render() {
    return (
      <Navbar
        className="scrolling-navbar"
        sticky="top"
        collapseOnSelect
        expand="lg"
        bg="dark"
        variant="dark"
      >
        <Navbar.Brand href="/">
          <Image style={{ maxHeight: 40 }} src={logo} alt="logo" />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          {JSON.parse(localStorage.getItem("user")) == null ? (
            <Nav className="ml-auto">
              <Link className="nav-link" to="/">
                Home
              </Link>
              <Link className="nav-link" to="/about">
                About
              </Link>
              <Link className="nav-link" to="/services">
                Services
              </Link>
              <Link className="nav-link" to="/contact">
                Contact
              </Link>
              <Link className="nav-link" to="/login">
                Login
              </Link>
              <Link className="nav-link" to="/register">
                Register
              </Link>
            </Nav>
          ) : (
            <>
              <Nav className="ml-auto">
                <Link className="nav-link" to="/Home">
                  Home
                </Link>
                <NavDropdown alignRight title="Funds" id="nav-dropdown">
                  <NavDropdown.Item as={Link} to="/funds">
                    Funds
                  </NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/send">
                    Send
                  </NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/add">
                    Add
                  </NavDropdown.Item>
                </NavDropdown>
                <Link className="nav-link" to="/explore">
                  Explore
                </Link>
                <Link className="nav-link" to="/transaction">
                  Transaction
                </Link>
                {JSON.parse(localStorage.getItem("customer")) ? (
                  <NavDropdown alignRight title="Coupon" id="nav-dropdown">
                    <NavDropdown.Item as={Link} to="/coupon">
                      Buy Coupon
                    </NavDropdown.Item>
                    <NavDropdown.Item as={Link} to="/coupons">
                      Coupons
                    </NavDropdown.Item>
                  </NavDropdown>
                ) : null}
              </Nav>
              <Nav>
                <NavDropdown
                  alignRight
                  title={
                    <span className="d-inline" style={{ alignSelf: "center" }}>
                      <Image
                        src={
                          "https://picsum.photos/180/180?text=" +
                          JSON.parse(localStorage.getItem("user")).name
                        }
                        roundedCircle
                        style={{
                          maxHeight: 40
                        }}
                      />{" "}
                      {JSON.parse(localStorage.getItem("user")).name}
                    </span>
                  }
                  id="nav-dropdown"
                >
                  {JSON.parse(localStorage.getItem("vendor")) ? (
                    <NavDropdown.Item as={Link} to="/Vendor">
                      <i className="fa fa-tachometer-alt text-dark" /> Dashboard
                    </NavDropdown.Item>
                  ) : null}
                  {JSON.parse(localStorage.getItem("admin")) ? (
                    <NavDropdown.Item as={Link} to="/admin">
                      <i className="fa fa-tachometer-alt text-dark" /> Dashboard
                    </NavDropdown.Item>
                  ) : null}
                  <NavDropdown.Item
                    as={Link}
                    to={
                      "/profile/" + JSON.parse(localStorage.getItem("user")).id
                    }
                  >
                    <i className="fa fa-user-circle text-dark" /> Profile
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item
                    as={Link}
                    to=""
                    onClick={() => {
                      localStorage.clear();
                      window.location.href = "/";
                    }}
                  >
                    <i className="fa fa-sign-out-alt text-dark" /> logout
                  </NavDropdown.Item>
                </NavDropdown>
              </Nav>
            </>
          )}
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default MyNavbar;
