import React from "react";
import "../App.css";
import { Container, Row, Col, Alert, Spinner, Button } from "react-bootstrap";
import Axios from "axios";
import { MDBDataTable } from "mdbreact";
import { Form } from "react-bootstrap";

class AdminTransaction extends React.Component {
  state = {
    userid: JSON.parse(localStorage.getItem("user")).id,
    Transaction: [],
    data: {
      columns: [
        {
          label: "Id",
          field: "id",
          sort: "asc",
          width: 150
        },
        {
          label: "Date",
          field: "Date",
          sort: "asc",
          width: 150
        },
        {
          label: "From Id",
          field: "fromuserid",
          sort: "asc",
          width: 270
        },
        {
          label: "From",
          field: "fromname",
          sort: "asc",
          width: 270
        },
        {
          label: "To Id",
          field: "touserid",
          sort: "asc",
          width: 270
        },
        {
          label: "To",
          field: "toname",
          sort: "asc",
          width: 270
        },
        {
          label: "Amount",
          field: "time",
          sort: "asc",
          width: 200
        },
        {
          label: "Validated By",
          field: "validatedby",
          sort: "asc",
          width: 200
        },
        {
          label: "Validated Date",
          field: "validateddate",
          sort: "asc",
          width: 200
        },
        {
          label: "Check",
          field: "check",
          sort: "asc"
        }
      ],
      rows: []
    },
    selectedids: [],
    TError: false,
    TEText: <Spinner animation="grow" size="lg" />,
    funds: <Spinner animation="grow" />
  };

  handleSelect(e) {
    this.setState({
      selectedids: {
        ...this.state.selectedids,
        [e.target.getAttribute("id")]:
          this.state.selectedids[e.target.getAttribute("id")] === true
            ? false
            : true
      }
    });
    console.log(this.state.selectedids[e.target.getAttribute("id")]);
  }

  componentDidMount() {
    this.setState({
      data: {
        columns: this.state.data.columns,
        rows: []
      }
    });
    Axios.get("https://mapay.ga/api/Transactions", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    }).then(res => {
      res.data.data.map(tran => {
        let data = { ...this.state.data };
        data.rows = [
          ...data.rows,
          {
            id: tran.id,
            time: tran.time,
            fromuserid: tran.fromuserid,
            fromname: tran.fromname,
            touserid: tran.touserid,
            toname: tran.toname,
            funds: tran.funds,
            validatedby: tran.validatedby,
            validateddate:
              tran.validateddate != null
                ? tran.validateddate.substring(0, 10) +
                  " " +
                  tran.validateddate.substring(11, 19)
                : null,
            action: (
              <Form.Check
                custom
                onChange={this.handleSelect.bind(this)}
                label=""
                type="checkbox"
                id={tran.id}
              />
            )
          }
        ];
        this.setState({ data: data });
        console.log(data);
        return 0;
      });
      this.setState({
        Transaction: res.data.data,
        TError: res.data.data.length > 0 ? true : false,
        TEText: res.data.data.length > 0 ? null : "There Is No Transactions"
      });
      console.log(res.data.data);
    });
  }

  deleteproduct(e) {
    this.setState({
      TError: false,
      TEText: <Spinner animation="grow" size="lg" />,
      funds: <Spinner animation="grow" />
    });
    const options = {
      validatedby: JSON.parse(localStorage.getItem("admin")).id,
      ids: Object.keys(this.state.selectedids).filter(val => {
        return this.state.selectedids[val] === true ? val : null;
      }),
      activate: e.target.value === "true" ? true : false
    };
    Axios.put("https://mapay.ga/api/verifytransaction", options, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    }).then(res => {
      console.log(res.data);
      this.componentDidMount();
      console.log(options);
      this.setState({ selectedids: [] });
    });
  }

  render() {
    return (
      <div className="App">
        <Container
          className="d-flex blue lighten-1 mb-5"
          style={{ minHeight: "20vh" }}
          fluid
        >
          <Container className="m-auto  text-center ">
            <h1>
              :) Hello Again, {JSON.parse(localStorage.getItem("user")).name}
            </h1>
          </Container>
        </Container>
        <Container className="my-5" style={{ minHeight: "70vh" }}>
          <Button
            onClick={() => {
              var mywindow = window.open("", "PRINT");
              mywindow.document.write("<style>*{margin:0;padding:0}</style>");
              mywindow.document.write(
                '<script src="https://kit.fontawesome.com/2f50f49aa5.js"></script><link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css" integrity="sha384-ggOyR0iXCbMQv3Xipma34MD+dH/1fQ784/j6cY/iJTQUOhcWr7x9JvoRxT2MZw1T" crossorigin="anonymous" />'
              );
              mywindow.document.write(
                document.getElementById("datatable").innerHTML
              );
              mywindow.document.write(
                '<script src="https://code.jquery.com/jquery-3.3.1.js"></script>'
              );
              mywindow.document.write(
                `<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/jquery-confirm/3.3.2/jquery-confirm.min.css"><script src="https://cdnjs.cloudflare.com/ajax/libs/jquery-confirm/3.3.2/jquery-confirm.min.js"></script><script>$.confirm({title: 'Confirm!',content: 'Do you wanna print?',buttons: {confirm: function () {$(".jconfirm-scrollpane").hide();window.print()},cancel: function () {window.close()}}});</script>`
              );
              mywindow.document.close();
              mywindow.focus();
            }}
          >
            PRINT
          </Button>
          <Button
            variant="success"
            value={true}
            onClick={this.deleteproduct.bind(this)}
          >
            activate
          </Button>
          <Button
            variant="danger"
            value={false}
            onClick={this.deleteproduct.bind(this)}
          >
            Desactivate
          </Button>
          <Row>
            <Col>
              {this.state.TError ? (
                <MDBDataTable
                  responsive
                  striped
                  bordered
                  hover
                  data={this.state.data}
                />
              ) : (
                <Alert variant="light">{this.state.TEText}</Alert>
              )}
            </Col>
          </Row>
          <div id="datatable" className="d-none p-5">
            <MDBDataTable
              responsive
              striped
              bordered
              hover
              searching={false}
              paging={false}
              data={this.state.data}
            />
          </div>
        </Container>
      </div>
    );
  }
}

export default AdminTransaction;
