import React from "react";
import "../App.css";
import { Container, Row, Col, Button, Spinner } from "react-bootstrap";
import { Link } from "react-router-dom";
import Axios from "axios";

class Funds extends React.Component {
  constructor() {
    super();
    this.state = {
      funds: <Spinner animation="grow" size="lg" />
    };
  }
  componentDidMount() {
    Axios.get(
      "https://mapay.ga/api/Wallets/" +
        JSON.parse(localStorage.getItem("user")).id,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }
    ).then(res => {
      console.log(res.data.data.funds);
      this.setState({ funds: res.data.data.funds });
    });
  }
  render() {
    const { funds } = this.state;
    return (
      <div className="App">
        <header className="App-header">
          <Container
            className="d-flex blue lighten-1 mb-5"
            style={{ minHeight: "20vh" }}
            fluid
          >
            <Container className="m-auto  text-center ">
              <h1>
                :) Hello Again, {JSON.parse(localStorage.getItem("user")).name}
              </h1>
            </Container>
          </Container>

          <Container className="d-flex mb-5" style={{ minHeight: "80vh" }}>
            <Container className="m-auto w-100 text-center ">
              <h1>Available Funds</h1>
              <hr color="white" style={{ height: "0.3px", width: "60%" }} />
              <h1>${funds}</h1>
              <hr color="white" style={{ width: "100%" }} />

              <Row>
                <Col md={4}>
                  <Link
                    className="btn btn-primary rounded-circle p-5"
                    as={Button}
                    to="/send"
                  >
                    <i className="fas fa-3x fa-hand-holding-usd" />
                  </Link>
                  <h4 className="m-2">Send money</h4>
                </Col>
                <Col md={4}>
                  <Link
                    className="btn btn-primary rounded-circle p-5"
                    as={Button}
                    to="/add"
                  >
                    <i className="fas fa-3x fa-plus" />
                  </Link>
                  <h4 className="m-2">Add money</h4>
                </Col>
                <Col md={4}>
                  <Link
                    className="btn btn-primary rounded-circle p-5"
                    as={Button}
                    to="/transaction"
                  >
                    <i className="fas fa-3x fa-exchange-alt" />
                  </Link>
                  <h4 className="m-2">See transactions</h4>
                </Col>
              </Row>
            </Container>
          </Container>
        </header>
      </div>
    );
  }
}
export default Funds;
