import React from "react";
import { Container, Jumbotron, Image } from "react-bootstrap";

function ProfileCover(props) {
  const name = props.username
    ? props.username
    : JSON.parse(localStorage.getItem("user")).name;
  return (
    <Container className="mb-5 text-left">
      <Jumbotron
        className="mask rgba-black-strong"
        style={{
          height: 300,
          backgroundImage:
            'url("https://picsum.photos/800/300?text=' + name + '")',
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center center"
        }}
        fluid
      >
        <div style={{ position: "relative", bottom: -100, left: +20 }}>
          <div className="d-block w-100 mask rgba-black-strong" />
          <Image
            style={{
              border: "5px solid",
              height: 180,
              width: 180,
              backgroundImage:
                'url("https://picsum.photos/180/180?text=' + name + '")',
              backgroundSize: "cover",
              backgroundRepeat: "no-repeat",
              backgroundPosition: "center center"
            }}
            src={"https://picsum.photos/180/180?text=" + name}
            className="border-light"
            roundedCircle
          />
          <h1 className="d-inline mx-5 text-light">
            {props.username != null
              ? props.username
              : JSON.parse(localStorage.getItem("user")).name}
          </h1>
        </div>
      </Jumbotron>
    </Container>
  );
}

export default ProfileCover;
