import React from "react";
import "../App.css";
import { Container, Tabs, Tab, Card, ListGroup } from "react-bootstrap";
import ProfileCover from "../com/ProfileCover";
import ProfileProducts from "../com/ProfileProducts";
import ProfileComment from "../com/ProfileComment";
import { Redirect } from "react-router-dom";
import Axios from "axios";

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: null,
      Redirect: false,
      key: "products",
      vendorid: null,
      data: {}
    };
  }

  componentDidMount() {
    if (this.props.match.params.id != null) {
      Axios.get("https://mapay.ga/api/user/" + this.props.match.params.id, {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          console.log(res.data);
          this.setState({
            username: res.data.user.username,
            vendorid: res.data.user.vendor ? res.data.user.vendor.id : null,
            address: res.data.user.address,
            data: res.data.user.vendor
          });
          console.log("t ", this.state.data);
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    }
  }
  render() {
    const { data } = this.state;
    return (
      <Container className="d-flex" style={{ minHeight: "100vh" }}>
        {this.state.Redirect ? (
          <Redirect to="/NotFound" />
        ) : (
          <Container fluid>
            <ProfileCover username={this.state.username} />
            {this.state.vendorid != null ? (
              <Container className="my-5 text-left">
                <Card>
                  <Card.Body>
                    <Tabs
                      className="mytab"
                      defaultActiveKey="products"
                      id="uncontrolled-tab-example"
                    >
                      <Tab eventKey="products" title="Products">
                        <ProfileProducts userid={this.props.match.params.id} />
                      </Tab>
                      <Tab eventKey="about" title="About">
                        <ListGroup className="my-5">
                          <ListGroup.Item>
                            <h3 className="d-inline">Name:</h3>{" "}
                            <h5 className="d-inline">{data.companyname}</h5>
                          </ListGroup.Item>
                          <ListGroup.Item>
                            <h3 className="d-inline">Description:</h3>{" "}
                            <h5 className="d-inline">{data.businessdetails}</h5>
                          </ListGroup.Item>
                          <ListGroup.Item>
                            <h3 className="d-inline">Type:</h3>{" "}
                            <h5 className="d-inline">{data.type}</h5>
                          </ListGroup.Item>
                          <ListGroup.Item>
                            <h3 className="d-inline">Address:</h3>{" "}
                            <h5 className="d-inline">{this.state.address}</h5>
                          </ListGroup.Item>
                        </ListGroup>
                      </Tab>
                    </Tabs>
                  </Card.Body>
                </Card>
                <ProfileComment vendorid={this.state.vendorid} />
              </Container>
            ) : null}
          </Container>
        )}
      </Container>
    );
  }
}
export default Profile;
