import React from "react";
import "./App.css";
import MyNavbar from "./com/navbar";
import MyFooter from "./com/footer";
import { BrowserRouter as Router } from "react-router-dom";
import Content from "./com/Routes";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.handler = this.handler.bind(this);
  }

  handler() {
    localStorage.clear();
    window.location.href = "/";
  }

  getauth() {
    return this.state.auth;
  }

  render() {
    return (
      <div className="App">
        <Router basename={process.env.PUBLIC_URL}>
          <MyNavbar
            handler={this.handler}
            getauth={localStorage.getItem("user")}
          />
          <Content auth={localStorage.getItem("user")} />
          <MyFooter />
        </Router>
      </div>
    );
  }
}

export default App;
