import React from "react";
import {
  Card,
  ListGroupItem,
  ListGroup,
  Button,
  Col,
  Form,
  Image
} from "react-bootstrap";
import Textarea from "react-textarea-autosize";
import Axios from "axios";
import { Link } from "react-router-dom";

class Post extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  comment(e) {
    e.preventDefault();
    const comment = {
      text: this.state[e.target.getAttribute("id")],
      postid: e.target.getAttribute("id"),
      userid: JSON.parse(localStorage.getItem("user")).id
    };
    if (comment != null) {
      Axios.post("https://mapay.ga/api/Postcomments", comment, {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        console.log(res);
      });
      const list = document.getElementById(
        "comment_P" + e.target.getAttribute("id")
      );

      const ListGroup = document.createElement("div");
      ListGroup.classList.add("list-group-item");
      const h5 = document.createElement("h5");
      const Image = document.createElement("img");
      Image.setAttribute(
        "src",
        "https://picsum.photos/180/180?text=" +
          JSON.parse(localStorage.getItem("user")).name
      );
      Image.setAttribute("style", "height: 30px; width: 30px;");
      Image.classList.add("rounded-circle");
      h5.appendChild(Image);
      h5.appendChild(
        document.createTextNode(
          " " + JSON.parse(localStorage.getItem("user")).name
        )
      );
      ListGroup.appendChild(h5);
      ListGroup.appendChild(document.createTextNode(comment.text));
      list.appendChild(ListGroup);
      this.setState({ [e.target.getAttribute("id")]: "" });
    }
  }

  handleComment(e) {
    this.setState({ [e.target.getAttribute("id")]: e.target.value });
    console.log(e.target.getAttribute("id"));
  }
  render() {
    return this.props.item.map(item => (
      <Card key={item.id} className="my-2 text-left">
        <Card.Header className="text-left">
          <h4>
            <Link className="text-dark" to={"/profile/" + item.userid}>
              <Image
                src={"https://picsum.photos/180/180?text=" + item.name}
                style={{ height: 50, width: 50 }}
                roundedCircle
              />{" "}
              {item.name}
            </Link>
          </h4>
        </Card.Header>
        <Card.Body>
          <Card.Text>{item.text}</Card.Text>
        </Card.Body>
        <Card.Img
          style={{ maxHeight: 500 }}
          variant="bottom"
          src={item.image}
        />
        <Card.Footer className="text-muted">
          <ListGroup id={"comment_P" + item.id} className="list-group-flush">
            {Object.values(item.comment.data).map(cmn => (
              <ListGroupItem key={cmn.id}>
                <h5>
                  <Link className="text-dark" to={"/profile/" + cmn.userid}>
                    <Image
                      src={"https://picsum.photos/180/180?text=" + cmn.name}
                      style={{ height: 30, width: 30 }}
                      roundedCircle
                    />{" "}
                    {cmn.name}
                  </Link>
                </h5>
                {cmn.text}
              </ListGroupItem>
            ))}
          </ListGroup>
          <Form
            id={item.id}
            onSubmit={this.comment.bind(this)}
            className="my-1"
          >
            <Form.Row className="align-items-center">
              <Col>
                <Textarea
                  id={item.id}
                  onChange={this.handleComment.bind(this)}
                  className="form-control"
                  style={{ resize: "none" }}
                  value={this.state[item.id]}
                  placeholder="Comment"
                />
              </Col>
              <Col md={"auto"}>
                <Button className="btn-sm btn-rounded" type="submit">
                  Comment
                </Button>
              </Col>
            </Form.Row>
          </Form>
        </Card.Footer>
      </Card>
    ));
  }
}

export default Post;
