import React from "react";
import { Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import Axios from "axios";

class SideAd extends React.Component {
  constructor(props) {
    super(props);
    this.state = { ad: [] };
  }
  componentDidMount() {
    Axios.get("https://mapay.ga/api/Products", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    })
      .then(res => {
        let cards = [];
        const length = res.data.data.length > 3 ? 3 : res.data.data.length;
        for (let i = 0; i < length; i++) {
          let element = res.data.data[i];
          cards = [...cards, element];
          console.log(element);
        }
        this.setState({ ad: cards, gotit: true });

        console.log("t", res.data.data);
        console.log(length);
      })
      .catch();
  }
  render() {
    return this.state.ad.map(res => (
      <Card className="my-2" key={res.id} as={Link} to={"/product/" + res.id}>
        <Card.Body>
          <Card.Title>{res.name}</Card.Title>
        </Card.Body>
        <Card.Img src={res.image} style={{ maxHeight: 500 }} variant="bottom" />
      </Card>
    ));
  }
}

export default SideAd;
