import React from "react";
import "../App.css";
import { Container, Alert, Spinner, Button } from "react-bootstrap";
import Axios from "axios";
import { MDBDataTable } from "mdbreact";

class AdminCoupons extends React.Component {
  state = {
    userid: JSON.parse(localStorage.getItem("user")).id,
    Transaction: [],
    data: {
      columns: [
        {
          label: "Id",
          field: "id",
          sort: "desc",
          width: 150
        },
        {
          label: "Funds",
          field: "funds",
          sort: "asc",
          width: 270
        },
        {
          label: "Coupon",
          field: "coupon",
          sort: "asc",
          width: 270
        },
        {
          label: "Exp Date",
          field: "time",
          sort: "asc",
          width: 150
        },
        {
          label: "User Id",
          field: "userid",
          sort: "asc",
          width: 150
        },
        {
          label: "Active",
          field: "active",
          sort: "asc",
          width: 200
        },
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    },
    TError: false,
    TEText: <Spinner animation="grow" size="lg" />,
    funds: <Spinner animation="grow" />
  };

  componentDidMount() {
    this.setState({
      data: {
        columns: this.state.data.columns,
        rows: []
      }
    });
    Axios.get("https://mapay.ga/api/Coupons", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    }).then(res => {
      res.data.data.map(coupon => {
        let data = { ...this.state.data };
        data.rows = [
          ...data.rows,
          {
            id: coupon.id,
            funds: coupon.funds,
            coupon: coupon.coupon,
            time: coupon.exp,
            userid: coupon.userid,
            active:
              coupon.active === 1 ? (
                <i className="fas fa-check success-ic" />
              ) : (
                <i className="fas fa-times danger-ic" />
              ),
            action:
              coupon.active === 1 ? (
                <Button
                  id={coupon.id}
                  variant="danger"
                  onClick={this.deleteproduct.bind(this)}
                  size="sm"
                >
                  desactivate
                </Button>
              ) : (
                <Button
                  id={coupon.id}
                  variant="success"
                  onClick={this.deleteproduct.bind(this)}
                  size="sm"
                >
                  activate
                </Button>
              )
          }
        ];
        this.setState({ data: data });
        console.log(data);
        return 0;
      });
      this.setState({
        Transaction: res.data.data,
        TError: res.data.data.length > 0 ? true : false,
        TEText: res.data.data.length > 0 ? null : "There Is No Products"
      });
      console.log(res.data.data);
    });
  }

  deleteproduct(e) {
    this.setState({
      TError: false,
      TEText: <Spinner animation="grow" size="lg" />,
      funds: <Spinner animation="grow" />
    });
    Axios.delete("https://mapay.ga/api/Coupons/" + e.target.id, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    }).then(res => {
      console.log(res.data);
      this.componentDidMount();
    });
  }

  render() {
    return (
      <div className="App">
        <Container className="my-5" style={{ minHeight: "70vh" }}>
          {this.state.TError ? (
            <MDBDataTable
              responsive
              striped
              bordered
              hover
              data={this.state.data}
            />
          ) : (
            <Alert variant="light">{this.state.TEText}</Alert>
          )}
        </Container>
      </div>
    );
  }
}

export default AdminCoupons;
